<template>
	<div>
		<ul class="nav">
			<router-link to="/" tag="li" :class="this.currentView=='/' ? 'high' : ''">
				全部患者
			</router-link>
			<router-link to="/patientMe" tag="li" :class="this.currentView=='/patientMe' ? 'high' : ''">
				我的患者
			</router-link>
		</ul>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				currentView: '/'
			}
		},
		mounted() {
			this.currentView = this.$route.path
		},
		watch: {
			$route(to, from) {
				this.currentView = this.$route.path
			}
		}
	}
</script>

<style lang="scss" scoped>
	.nav{
		background:rgba(240,241,242,1);
		font-size:14px;
		font-weight:bold;
		color:rgba(74,74,74,1);
		overflow: hidden;
		li{
			height: 60px;
			line-height: 60px;
			float: left;
			width: 50%;
			box-sizing: border-box;
			cursor: pointer;
			&:nth-child(1){
				text-align: right;
				padding-right: 70px;
			}
			&:nth-child(2){
				text-align: left;
				padding-left: 70px;
			}
		}
		.high{
			background-color: #fff;
			color:rgba(96,180,111,1);
		}
	}
</style>
