<script>
	export default {
		data() {
			return {
				currentView: '/',
				showAccount: false,
				showModal: false
			}
		},
		mounted() {
			this.tabChange()
		},
		methods: {
			tabChange() {
				switch (this.$route.path) {
					case '/patientMe':
						this.currentView = '/'
						break;
					case '/caseResearch':
					case '/searchList':
						this.currentView = '/caseResearch'
						break;
					case '/sleepDurtor':
					case '/clinicalDiagnosis':
					case '/auditRecord':
					case '/operativeNote':
					case '/hours24':
					case '/anaesthesia':
					case '/treatmentHistory':
					case '/stayInHospital':
					case '/laborWaitingForDelivery':
					case '/outpatientPrescription':
					case '/intakeAndOutput':
					case '/courseOfDisease':
					case '/nursingRecords':
					case '/CDA':
					case '/transfusionalRecording':
						this.currentView = '/inspectionReport'
						break;
					case '/userManagement':
						this.currentView = '/roleManagement'
						break;

					default:
						this.currentView = this.$route.path
				}
			},
			quit() {
				this.$router.push({
					name: 'login'
				})
			}
		},
		watch: {
			$route(to, from) {
				this.tabChange()
			}
		}
	}
</script>

