import Vue from 'vue'
import VueRouter from 'vue-router'
import login from '../views/login/login'
import view360 from '../views/360-view/360-view'
import inspectionReport from '../views/360-view/inspection-report'
import sleepDurtor from '../views/360-view/sleep-ductor'
import clinicalDiagnosis from '../views/360-view/clinical-diagnosis'
import auditRecord from '../views/360-view/audit-record'
import CDA from '../views/360-view/CDA'
import transfusionalRecording from '../views/360-view/transfusional-recording'
import operativeNote from '../views/360-view/operative-note'
import hours24 from '../views/360-view/24-hours'
import anaesthesia from '../views/360-view/anaesthesia'
import treatmentHistory from '../views/360-view/treatment-History'
import stayInHospital from '../views/360-view/stay-in-hospital'
import laborWaitingForDelivery from '../views/360-view/labor-waiting-for-delivery'
import outpatientPrescription from '../views/360-view/outpatient-prescription'
import intakeAndOutput from '../views/360-view/intake-and-output'
import courseOfDisease from '../views/360-view/course-of-disease'
import nursingRecords from '../views/360-view/nursing-records'

import caseResearch from '../views/case-research/case-research'
import searchList from '../views/case-research/searchList'

import patientOnline from '../views/patient-online/patient-online'
import patientAll from '../views/patient-online/patient-all'
import patientMe from '../views/patient-online/patient-me'

import systemMangeto from '../views/system-mangeto/system-mangeto'
import roleManagement from '../views/system-mangeto/role-management'
import userManagement from '../views/system-mangeto/user-management'


Vue.use(VueRouter)

const routes = [{
    path: '/login',
    name: 'login',
    component: login
  }, {
    path: '/',
    name: 'patientOnline',
    component: patientOnline,
    children: [{
        path: '/',
        name: 'patientAll',
        component: patientAll
      },
      {
        path: '/patientMe',
        name: 'patientMe',
        component: patientMe
      }
    ]
  },
  {
    path: '/view360',
    name: 'view360',
    component: view360,
    children: [{
      path: '/inspectionReport',
      name: 'inspectionReport',
      component: inspectionReport
    }, {
      path: '/sleepDurtor',
      name: 'sleepDurtor',
      component: sleepDurtor
    }, {
      path: '/clinicalDiagnosis',
      name: 'clinicalDiagnosis',
      component: clinicalDiagnosis
    }, {
      path: '/auditRecord',
      name: 'auditRecord',
      component: auditRecord
    }, {
      path: '/CDA',
      name: 'CDA',
      component: CDA
    }, {
      path: '/transfusionalRecording',
      name: 'transfusionalRecording',
      component: transfusionalRecording
    }, {
      path: '/operativeNote',
      name: 'operativeNote',
      component: operativeNote
    }, {
      path: '/hours24',
      name: 'hours24',
      component: hours24
    }, {
      path: '/anaesthesia',
      name: 'anaesthesia',
      component: anaesthesia
    }, {
      path: '/treatmentHistory',
      name: 'treatmentHistory',
      component: treatmentHistory
    }, {
      path: '/stayInHospital',
      name: 'stayInHospital',
      component: stayInHospital
    }, {
      path: '/laborWaitingForDelivery',
      name: 'laborWaitingForDelivery',
      component: laborWaitingForDelivery
    }, {
      path: '/outpatientPrescription',
      name: 'outpatientPrescription',
      component: outpatientPrescription
    }, {
      path: '/intakeAndOutput',
      name: 'intakeAndOutput',
      component: intakeAndOutput
    }, {
      path: '/courseOfDisease',
      name: 'courseOfDisease',
      component: courseOfDisease
    }, {
      path: '/nursingRecords',
      name: 'nursingRecords',
      component: nursingRecords
    }]
  },
  {
    path: '/caseResearch',
    name: 'caseResearch',
    component: caseResearch
  },
  {
    path: '/searchList',
    name: 'searchList',
    component: searchList
  },
  {
    path: '/systemMangeto',
    name: 'systemMangeto',
    component: systemMangeto,
    children: [{
        path: '/roleManagement',
        name: 'roleManagement',
        component: roleManagement
      },
      {
        path: '/userManagement',
        name: 'userManagement',
        component: userManagement
      }
    ]
  },

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
