<template>
  <div class="login">
    <div class="loginCard">
      <img class="loginzpd" src="../../assets/img/loginzpd.png" alt="">
      <h2>XXXX医院临床视图</h2>
      <div class="loginShadow">
        <img class="loginLogo" src="../../assets/img/loginLogo.png" alt="">
        <h3>登录</h3>
        <div class="inputCard" style="margin-top: 31px;">
          <div class="inputIcon">
            <img class="userName" src="../../assets/img/userName.png" alt="">
          </div>
          <input type="text" placeholder="输入用户名" v-model="userName">
        </div>
        <div class="inputCard" style="margin-top: 16px;">
          <div class="inputIcon">
            <img class="userName" src="../../assets/img/pwd.png" alt="">
          </div>
          <input type="password" placeholder="输入密码" v-model="passWord">
        </div>
        <div class="editCard">
          <el-checkbox v-model="remember">记住我</el-checkbox>
          <a href="#">忘记密码？</a>
        </div>
        <a href="#" class="loginBtn" @click="login">登录</a>
      </div>
      <p>©2015-2019 北京中普达版权所有</p>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        userName: '',
        passWord: '',
        remember: false
      }
    },
    created() {
      if (localStorage.getItem("remember") == 'true') {
        this.remember = true
        this.userName = localStorage.getItem("userName")
        this.passWord = localStorage.getItem("passWord")
      }
    },
    methods: {
      login() {
        if (this.remember) {
          localStorage.setItem("remember", true);
          localStorage.setItem("userName", this.userName);
          localStorage.setItem("passWord", this.passWord);
        } else {
          localStorage.removeItem("remember");
          localStorage.removeItem("userName");
          localStorage.removeItem("passWord");
        }
        this.$router.push({
          name: 'patientOnline'
        })
      }
    }
  }
</script>

